















































































import { Component, Watch, Prop, Vue } from 'vue-property-decorator';
import CStatistics from '@/components/links/CStatistics.vue';
import CButton from '@/components/CButton.vue';
import CSelectorSample from '@/components/CSelectorSample.vue';
import MathUtil from '@/typescript/MathUtil.ts'
import StatisticsCalc from '@/typescript/StatisticsCalc.ts'

@Component({
  components: {
    CStatistics,
    CButton,
    CSelectorSample,
  },
  head: {
    title: {
      inner: '一様乱数',
    },
  },
})
export default class UniformRandom extends Vue {
  // Data
  private dataLinkTitle: string = '関連リンク一覧';
  private dataButtonText: string = '出力';
  private dataKeyRegExps: {
    sample: RegExp,
  } = {
    sample: /\{ *sample\ *\}/g,
  };
  private dataFormat: string = '';
  private dataSelectedFormatExampleIndex: number = 0;
  private dataFormatExamples: {
    name: string,
    caption: string,
    content: string,
  }[] = [
    {
      name: 'none',
      caption: '',
      content: '',
    },
    {
      name: 'comma',
      caption: 'カンマ区切り',
      content: '{ sample },',
    },
    {
      name: 'new_line',
      caption: '改行区切り',
      content: '{ sample }\n',
    },
    {
      name: 'semicolon',
      caption: 'セミコロン区切り',
      content: '{ sample }:',
    },
    {
      name: 'slash',
      caption: 'スペース区切り',
      content: '{ sample } ',
    },
  ];
  private dataSelectedSampleExampleIndex: number = 0;
  private dataSampleExamples: {
    name: string,
    caption: string,
    content: string,
  }[] = [
    {
      name: 'none',
      caption: '',
      content: ',,,',
    },
    {
      name: 'interval_0_1',
      caption: '0.000から1.000（デフォルト）',
      content: '1000,0,1,3',
    },
    {
      name: 'interval_-1_1',
      caption: '-1.000から1.000',
      content: '1000,-1,1,3',
    },
    {
      name: 'dice',
      caption: 'サイコロ',
      content: '100,1,6,0',
    },
    {
      name: 'coin',
      caption: 'コイントス',
      content: '100,0,1,0',
    },
  ];
  private dataSampleSize: string = '1000';
  private dataMax: string = '1';
  private dataMin: string = '0';
  private dataDecimalPlace: string = '3';
  private dataText: string = '';

  private created(): void {
  }

  // Methods
  private listenSampleSizeInput($event: Event): void {
    let number: string = this.dataSampleSize;
    this.dataSampleSize = (<HTMLInputElement>(<any>$event).target).value;
    if (!MathUtil.isNaturalNumberIncludingZero(this.dataSampleSize)
      || Number(this.dataSampleSize) < 1) {
      this.dataSampleSize = number;
    }
  }
  private listenMaxInput($event: Event): void {
    let number: string = this.dataMax;
    this.dataMax = (<HTMLInputElement>(<any>$event).target).value;
    if (!MathUtil.isRealNumber(this.dataMax)
      || Number(this.dataMax) <= Number(this.dataMin)) {
      this.dataMax = number;
    }
  }
  private listenMinInput($event: Event): void {
    let number: string = this.dataMin;
    this.dataMin = (<HTMLInputElement>(<any>$event).target).value;
    if (!MathUtil.isRealNumber(this.dataMin)
      || Number(this.dataMax) <= Number(this.dataMin)) {
      this.dataMin = number;
    }
  }
  private listenDecimalPlaceInput($event: Event): void {
    let number: string = this.dataDecimalPlace;
    this.dataDecimalPlace = (<HTMLInputElement>(<any>$event).target).value;
    if (!MathUtil.isNaturalNumberIncludingZero(this.dataDecimalPlace)) {
      this.dataDecimalPlace = number;
    }
  }
  private listenInput($event: Event): void {
    this.dataFormat = (<HTMLInputElement>(<any>$event).target).value;
  }
  private listenOnFormatExampleSelect($event: Event): void {
    let index: number = Number(<any>$event);
    this.dataFormat = this.dataFormatExamples[index].content;
    this.dataSelectedFormatExampleIndex = index;
  }
  private listenOnSampleExampleSelect($event: Event): void {
    let index: number = Number(<any>$event);
    let samples: string[] = this.dataSampleExamples[index].content.split(',');
    this.dataSampleSize = samples[0];
    if (samples[1] !== '') {
      this.dataMin = samples[1];
    }
    if (samples[2] !== '') {
      this.dataMax = samples[2];
    }
    this.dataDecimalPlace = samples[3];
    this.dataSelectedSampleExampleIndex = index;
  }
  private outputText(): void {
    this.dataText = '';
    if (this.dataFormat !== ''
      && this.dataSampleSize !== '' 
      && this.dataMax !== '' 
      && this.dataMin !== ''
      && this.dataDecimalPlace !== '') {
      let randoms: number[] = [];
      for (let i: number = 0; i < Number(this.dataSampleSize); i++) {
        randoms.push(
          MathUtil.randomFloat(
            Number(this.dataMin),
            Number(this.dataMax),
            Number(this.dataDecimalPlace)
          )
        );
      }
      this.formatSamples(randoms);
    }
  }
  private formatSamples(randoms: number[]): void {
    let text: string = '';
    for (let i: number = 0; i < randoms.length; i++) {
      let randomText: string = this.dataFormat;
      randomText = randomText.replace(
        this.dataKeyRegExps.sample,
        MathUtil.toFixed(randoms[i], Number(this.dataDecimalPlace))
        // String(randoms[i])
      );
      text += randomText;
    }
    this.dataText = text;
  }
}
